import React, { useState } from "react";
import { Modal } from "../modal/page";
import { TicketInfoRow } from "../tr/page";
import { Button } from "../button/page";

export const TicketLayout = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [tickets, setTickets] = useState<
    Array<{
      id: number;
      title: string;
      description: string;
      status: string;
      priority: string;
    }>
  >([]);

  const fetchTickets = async () => {
    try {
      const response = await fetch("http://localhost:3001/tickets");
      const data = await response.json();
      setTickets(data);
    } catch (error) {
      console.error("Erro ao buscar chamados:", error);
    }
  };

  const addTicketToTable = (newTicket: {
    id: number;
    title: string;
    description: string;
    status: string;
    priority: string;
  }) => {
    setTickets((prevTickets) => [...prevTickets, newTicket]);
  };

  React.useEffect(() => {
    fetchTickets();
  }, []);

  return (
    <div className="px-10 py-6 flex flex-col">
      <div className="flex justify-between items-center">
        <h1 className="font-montserrat text-2xl font-semibold text-gray-800">
          Chamados
        </h1>
        <div className="w-36 h-12">
          <Button text="New Desk" onClick={() => setIsModalOpen(true)}></Button>
        </div>
      </div>
      <table className="table-auto border-collapse w-full mt-4 border border-gray-300 shadow-xl">
        <thead>
          <tr className="bg-gray-100 text-gray-800">
            <th className="border border-gray-300 px-4 py-2">ID</th>
            <th className="border border-gray-300 px-4 py-2">Título</th>
            <th className="border border-gray-300 px-4 w-40 py-2">
              Prioridade
            </th>
            <th className="border border-gray-300 px-4 py-2">Status</th>
          </tr>
        </thead>
        <tbody>
          {tickets
            .map((ticket) => (
              <TicketInfoRow
                description={ticket.description}
                priority={ticket.priority}
                key={ticket.id}
                id={ticket.id}
                title={ticket.title}
                status={ticket.status}
              />
            ))
            .reverse()}
        </tbody>
      </table>
      {isModalOpen && (
        <Modal
          setIsModalOpen={setIsModalOpen}
          onTicketCreated={addTicketToTable}
        />
      )}
    </div>
  );
};
