import React from "react";

interface ModalTicketInfoProps {
  setIsModalOpen: React.Dispatch<React.SetStateAction<boolean>>;
  ticket: {
    id: number;
    title: string;
    priority: string;
    status: string;
    description: string;
  };
  onClose: () => void;
  onDelete: (id: number) => void; // Adiciona a função onDelete
}

export const ModalTicketInfo: React.FC<ModalTicketInfoProps> = ({
  setIsModalOpen,
  ticket: { id, title, priority, status, description },
  onDelete, // Adiciona onDelete na desestruturação
}) => {
  const [isEditing, setIsEditing] = React.useState(false);
  const [editedTicket, setEditedTicket] = React.useState({
    title,
    priority,
    status,
    description,
  });

  const handleSave = () => {
    // Implement save functionality here
    console.log("Save button clicked", editedTicket);
    setIsEditing(false);
  };

  const handleDelete = () => {
    onDelete(id); // Chama a função onDelete com o id do ticket
    setIsModalOpen(false); // Fecha o modal após a exclusão
  };

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { id, value } = e.target;
    setEditedTicket((prev) => ({ ...prev, [id]: value }));
  };

  return (
    <div
      className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50"
      role="dialog"
      aria-modal="true"
    >
      <div className="bg-white p-6 rounded-lg shadow-lg w-96">
        <form>
          <div className="mb-4">
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="id"
            >
              ID
            </label>
            <input
              disabled
              id="id"
              type="text"
              value={id}
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            />
          </div>
          <div className="mb-4">
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="title"
            >
              Title
            </label>
            <input
              id="title"
              type="text"
              value={editedTicket.title}
              onChange={handleChange}
              disabled={!isEditing}
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            />
          </div>
          <div className="mb-4">
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="priority"
            >
              Priority
            </label>
            <input
              id="priority"
              type="text"
              value={editedTicket.priority}
              onChange={handleChange}
              disabled={!isEditing}
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            />
          </div>
          <div className="mb-4">
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="status"
            >
              Status
            </label>
            <input
              id="status"
              type="text"
              value={editedTicket.status}
              onChange={handleChange}
              disabled={!isEditing}
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            />
          </div>
          <div className="mb-4">
            <label
              className="block text-gray-700 text-sm font-bold mb-2"
              htmlFor="description"
            >
              Description
            </label>
            <textarea
              id="description"
              value={editedTicket.description}
              onChange={handleChange}
              disabled={!isEditing}
              className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            />
          </div>
          <div>
            <button
              type="button"
              className="px-4 py-2 underline text-gray-700 hover:text-red-600"
              onClick={() => setIsModalOpen(false)} // Fecha o modal
            >
              Fechar
            </button>
            <button
              type="button"
              className="px-4 py-2 underline text-gray-700 hover:text-blue-600"
              onClick={() => setIsEditing(true)} // Habilita a edição
            >
              Editar
            </button>
            {isEditing && (
              <button
                type="button"
                className="px-4 py-2 underline text-gray-700 hover:text-green-600 ml-4"
                onClick={handleSave} // Salva as edições
              >
                Salvar
              </button>
            )}
            <button
              type="button"
              className="px-4 py-2 underline text-gray-700 hover:text-red-600 ml-4"
              onClick={handleDelete} // Exclui o ticket
            >
              Excluir
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};
